import {NgModule, ModuleWithProviders} from '@angular/core';
import {CommonModule} from '@angular/common';
import {AdaptAlertComponent} from './alert.component';

@NgModule({
  imports: [CommonModule],
  declarations: [AdaptAlertComponent],
  exports: [AdaptAlertComponent]
})

export class AdaptAlertModule {
  /**
   * Importing with '.forRoot()' is no longer necessary, you can simply import the module.
   * Will be removed in a short while
   *
   * @deprecated 11.16.0
   */
  static forRoot(): ModuleWithProviders<AdaptAlertModule> {
    return {
      ngModule: AdaptAlertModule
    };
  }
}

export * from './alert.component';
export * from './alert.config';
